*{
    box-sizing: border-box;
}
html,body{
    height: 100%;
    width: 100%;
}

body {
    background: #03001C;
}

.stages{
    height: 50px;
    width: 80%;
    margin:auto;
    display: flex;
    direction: row;
    justify-content: space-evenly;
    align-items: center;
    background-color: #181344;
}

.autonomousbtn{
    width: 100%;
    height:100%;
    border: none;
}

.autonomousbtn:hover{
    cursor: pointer;
}

.teleopbtn{
    width: 100%;
    height:100%;
    border: none;
}

.teleopbtn:hover{
    cursor: pointer;
}

.score{
    width:30%;
    height:100%;
}

.total{
    text-align: left;
    color: #FFFFFF;
    font-size: 1.7rem;
    font-weight: 700;
}

.partialscore{
    text-align: left;
    color: #FFFFFF;
    font-size: 1.2rem;
    font-weight: 300;
}

.container{
    width:100%;
    height:100%;
}

.pallette{
    height: 50px;
    width: 80%;
    margin:auto;
    margin-top:50px;
    display: flex;
    direction: row;
    justify-content: space-evenly;
    align-items: center;
    background-color: #03001C;
}

.colorblock{
    cursor:pointer;
    display: inline-flex;
    height: 25px;
    margin: 0 7px 0 0;
    position: relative;
    width: 43px;
    border-radius: 10%;
    border-style: none;
}

.deletepixel{
    background-image:url("../images/erasericon.png");
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    cursor:pointer;
    display: inline-flex;
    height: 50px;
    width: 50px;
    border-style: none;
    rotate: -45deg;
}


.pixelbuttons {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 40%;
    padding-top:73px;
}

.buttonsandscore{
    margin-top:30px;
    display: flex;
    align-items: start;
    justify-content: center; 
    flex-direction: row;
    width: 100%;
}

.pixelrow {
    height: 90px;
    margin-top: -37px;
}

.pixel {
    display: inline-flex;
    height: 29px;
    margin: 0 7px 0 0;
    position: relative;
    width: 52px;
    border-radius: 10%;
    border-style: none;
}

.pixel:hover{
    cursor: pointer;
}
